$brand-primary: #ffffff;
@import './basecolors/basecolors_frontier';
@import "../wwwroot/lib/bootstrap/scss/bootstrap_landing";
@import './breakpoints.scss';


/* Footer Styles */
@import "../Styles/Layout/_footer.scss";

@import "../Styles/Account/_login.scss";

/* --Layout Styles-- */
@media (min-width: 768px) {
    html {
    }
}
/* --Layout Styles-- */

body {
    background: $brand-primary;
}

a {
    cursor: pointer;
}

.tel {
    white-space: nowrap;
}

a, a:link, a:visited, a:active {
    text-decoration: none;
    color: #000;
}

a:hover, a:focus {
    color: #000;
    text-decoration:underline;
}

.alert {
    border-left: 10px solid;
}

.btn {
    border-radius: 0;
    text-transform: uppercase;
}

.col-form-label {
    font-weight: 800;
    color: #333;
}



/* Header Styles */
@import "../Styles/Layout/_header";



.btnWidth {
    width: 150px !important;
}

.btnWidthSM {
    width: 250px !important;
    margin-left: 130px;
}

::-webkit-input-placeholder {
    font-size: small;
    font-align: center;
    color: lightgrey;
}

.inputRow input::-webkit-input-placeholder {
    //padding:0px 15px 0px 15px;
    text-align: center;
    padding-left: 20px;
    font-size: small;
    font-align: center;
    color: lightgrey;
}

::-moz-placeholder {
    font-size: small;
    font-align: center;
    color: lightgrey;
}
/* enable absolute positioning */
.inner-addon {
    position: relative;

    .faRight {
        position: absolute;
        top: 0;
        right: 11px;
        background-color: white;
        width: 10px;
    }
}

/* style icon */
.inner-addon .fa {
    position: absolute;
    padding: 10px;
    pointer-events: none;
}

/* align icon */
.left-addon .fa {
    left: 0px;
}

.right-addon .fa {
    right: 0px;
}

/* add padding  */
.left-addon input {
    padding-left: 30px;
}

.right-addon input {
    padding-right: 30px;
}

.btnreg {
    margin-top: 25px;
    font-size: 10px;
    padding: 15px 45px 15px 45px;
    background-color: blue;
    box-shadow: 5px 5px 2px black;
    border: 1.5px solid black;
    border-radius: 0px;
    border-style: outset;
    border-color: black;
}

.inputRow {
    padding: 10px 50px 10px 70px;
    width: 450px !important;

    input {
        border: none;
        border-bottom: 1px solid #000000;
        border-bottom-color: #000000;
        //width:400px !important;
    }
}

@import "../Styles/Account/_register.scss";

@import "../Styles/Account/_reset-password.scss";