﻿

.login-container {
    margin-top: 46px;
    margin-bottom: 90px;
}

.login-container > .row {
    display: flex;
}

.ui-light {
    background-color: rgba(251,248,249,0.45);

    &:hover {
        background-color: rgba(251,248,249,0.85);
        transition: all 1s ease-in;
    }
}

.ui-subhead {
    font-size: 20px;
}

h4.media-heading {
    font-size: 21px;
}

.ui-dark {
    background-color: $loginpanelfontcolor-base;

    hr {
        border-color: rgba(251,248,249,0.15);
    }

    p {
        color: #000;
    }

    * {
        color: #fff;
    }

    .navbar-inverse {
        border-radius: 0;
        height: 32px;
    }
}

container.login-container, .hero-wrapper {
    min-height: 650px;
}


.my-gexa-login-form {
    padding: 2em;
    //height:100%;
    //width:100%;
    input,
    .input-group-addon {
        //border:0;
        //border-radius: 0;
        //border-bottom: 1px solid #000;
        box-shadow: none;
        outline: 0;
        background: transparent;
    }

    .input:disabled {
        cursor: pointer;
    }

    .form-control:focus {
        box-shadow: none;
        border-color: #000;
    }

    .social-media-icon {
        a {
            margin-left: .4em;
            text-decoration: underline;
        }
    }

    h2 {
        font-size: 28px;
    }
}

